import { mealStatusMap } from '../constants';

// --- File Download Helper (CSV Export) ---
export const downloadCSV = (data, type) => {
    let csvContent = "data:text/csv;charset=utf-8,\uFEFF";
    if (type === "bp") {
        csvContent += ",,(),(), \n";
        data.forEach((row) => {
            csvContent += `${row.date},${row.time},${row.systolic},${row.diastolic},${row.medsTaken ? "" : ""
                }\n`;
        });
    } else {
        csvContent += ",, , (mg/dL), \n";
        data.forEach((row) => {
            csvContent += `${row.date},${row.time},${mealStatusMap[row.mealStatus] || row.mealStatus
                },${row.level},${row.medsTaken ? "" : ""}\n`;
        });
    }
    const encodedUri = encodeURI(csvContent);
    const link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute(
        "download",
        `${type === "bp" ? "" : ""}__${new Date()
            .toISOString()
            .slice(0, 10)}.csv`
    );
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
};
